/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a,
.footer-container button, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  box-sizing: border-box;
}
*::selection {
  background-color: rgba(255, 221, 0, 0.5);
}

html {
  scroll-behavior: smooth;
}

body {
  color: #292B2D;
  font-family: "Inter";
  font-size: 20px;
  font-size: calc(16px + (4) * (100vw - 400px) / (860));
}
@media (min-width: 1260px) {
  body {
    font-size: 20px;
  }
}
@media (max-width: 400px) {
  body {
    font-size: 16px;
  }
}

h1 {
  margin-bottom: 30px;
  font-size: 60px;
  font-size: calc(40px + (20) * (100vw - 400px) / (860));
  font-weight: bold;
}
@media (min-width: 1260px) {
  h1 {
    font-size: 60px;
  }
}
@media (max-width: 400px) {
  h1 {
    font-size: 40px;
  }
}

h2 {
  margin-bottom: 30px;
  font-size: 40px;
  font-size: calc(30px + (10) * (100vw - 400px) / (860));
  font-weight: bold;
}
@media (min-width: 1260px) {
  h2 {
    font-size: 40px;
  }
}
@media (max-width: 400px) {
  h2 {
    font-size: 30px;
  }
}

p {
  font-size: 20px;
  font-size: calc(16px + (4) * (100vw - 400px) / (860));
  line-height: 1.6em;
}
@media (min-width: 1260px) {
  p {
    font-size: 20px;
  }
}
@media (max-width: 400px) {
  p {
    font-size: 16px;
  }
}
p:not(:last-child) {
  margin-bottom: 30px;
}

strong {
  font-weight: bold;
}

a, .footer-container button {
  color: #292B2D;
  text-decoration: underline;
}
a:hover, .footer-container button:hover,
a:hover a,
.footer-container button:hover a,
a:hover .footer-container button,
.footer-container a:hover button,
.footer-container button:hover button {
  text-decoration-style: wavy;
}

