.index-hero {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 30px;
  scroll-snap-align: center;
}
@media (max-width: 62em) {
  .index-hero {
    scroll-snap-align: start;
  }
}

.index-inner {
  align-items: center;
}
.index-inner > div {
  grid-column: span 6;
}
@media (min-width: 62em) {
  .index-inner > div:last-child {
    text-align: right;
  }
}
@media (max-width: 62em) {
  .index-inner > div {
    grid-column: span 12;
  }
}
.index-inner h1 {
  margin-top: 15px;
}
.index-inner p {
  margin-bottom: 30px;
}
.index-inner svg {
  width: auto;
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

.index-pill {
  display: inline-block;
  margin-top: 20px;
  padding: 5px 18px;
  color: #564B00;
  font-size: 14px;
  font-size: calc(12px + (2) * (100vw - 400px) / (860));
  line-height: normal;
  background-color: rgba(255, 221, 0, 0.3);
  border-radius: 13px;
}
@media (min-width: 1260px) {
  .index-pill {
    font-size: 14px;
  }
}
@media (max-width: 400px) {
  .index-pill {
    font-size: 12px;
  }
}
.index-pill a, .index-pill .footer-container button, .footer-container .index-pill button {
  color: #564B00;
}

.index-section {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 220px 30px;
  scroll-snap-align: center;
}
@media (max-width: 62em) {
  .index-section {
    scroll-snap-align: start;
  }
}
.index-section .grid > div:first-child, .index-section .index-inner > div:first-child {
  grid-column: span 6;
}
@media (max-width: 62em) {
  .index-section .grid > div:first-child, .index-section .index-inner > div:first-child {
    grid-column: span 12;
  }
}
.index-section.dark {
  background-color: #F1F1F1;
}

