.footer-wrap {
  position: relative;
  padding: 83px 30px 140px;
  overflow: hidden;
}
.footer-wrap:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 85px;
  background-image: url(../svg/FooterDivider.svg);
  background-repeat: repeat;
}

.footer-container {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-top: 130px;
}
.footer-container button {
  line-height: 1.6em;
  appearance: none;
  background: none;
  border: none;
  border-radius: 0;
  cursor: pointer;
}
@media (max-width: 48em) {
  .footer-container {
    display: block;
  }
  .footer-container ul {
    margin-bottom: 30px;
  }
}